import React from 'react';
// import './Modal.scss';
import "./Modal.css"
import { useState } from "react";
import {useEffect} from "react";
const modalRef = React.createRef()



function Details(props){
    useEffect(() => {
        if (props.trigger){
            document.body.style.position = 'fixed';
            document.body.style.top= `-${window.scrollY}px`;
            
        }
        else{
            document.body.style.position = '';
            document.body.style.top ='';
        }
    }, [props.trigger])
    function similarFilter(datum){
        const volumeselected = 1
        if (props.option.volumefilter1 != 1){
            return datum[props.option.volumefilter]*datum[props.option.volumefilter1]*datum[props.option.volumefilter2] == props.selecteditem[props.option.volumefilter]*props.selecteditem[props.option.volumefilter1]*props.selecteditem[props.option.volumefilter2] && datum[props.option.energyfilter] <(props.selecteditem[props.option.energyfilter])+5 && datum[props.option.energyfilter] >(props.selecteditem[props.option.energyfilter])-5 && datum["model_number"] != props.selecteditem["model_number"];

        }
        else if(props.option.volumefilter1==1){
            return datum[props.option.volumefilter] == props.selecteditem[props.option.volumefilter] && datum[props.option.energyfilter] <(props.selecteditem[props.option.energyfilter])+5 && datum[props.option.energyfilter] >(props.selecteditem[props.option.energyfilter])-5 && datum["model_number"] != props.selecteditem["model_number"]

        }
        console.log(volumeselected)
    }

    function similarItemsReset(newitem){
        props.openPopup(newitem)
        // modalRef.current.scrollTop = 0;
    }
    // console.log(props.trigger)
    // console.log(trigger)
    return (props.trigger) ? (
        <div class = "Details">
            <div class = "modal12">
            {/* <label for="modal" className="modal-bg"></label> */}
                <div className="item-content" ref={ modalRef }>
                    <button className = "exit" onClick = {props.onClose} >x</button>

                        {Object.keys(props.selecteditem).map(attribute=><p>{attribute}: {Object.values(props.selecteditem[attribute])}</p>)}
                    <footer>
                        <div className="container-fluid py-2">
                            <h2>Similar Items</h2>
                            <div className="row flex-row flex-nowrap" >
                                {/* {console.log(props.data)} */}
                                {props.data.filter(datum => similarFilter(datum)).map(
                                    item => 
                                    <div class="card" style={{width: "18rem"}}   >
                                        <div class="card-body">
                                        <h5 class="card-title" onClick = {()=>similarItemsReset(item)}>{item["brand_name"] + " " + item["model_number"]}</h5>
                                        <p class="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
                                        <p class="card-text"><small class="text-muted">Last updated 3 mins ago</small></p>
                                        </div>
                                    </div>                            
                                    )} 
                            </div>
                        </div>
                    </footer>
                </div>
            </div>

        </div>
    ):"";
}

export default Details

